"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import Link from "next/link"
import { getSubject, getChapters } from "@/lib/firestore"
import type { Subject, Chapter } from "@/types"

export default function SubjectChapters({ 
  params 
}: { 
  params: { subject: string }
}) {
  const [subject, setSubject] = useState<Subject | null>(null)
  const [chapters, setChapters] = useState<Chapter[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState("")

  useEffect(() => {
    const fetchData = async () => {
      try {
        const subjectData = await getSubject(params.subject)
        if (!subjectData) {
          setError("Subject not found")
          return
        }
        setSubject(subjectData)

        const chaptersData = await getChapters(params.subject)
        setChapters(chaptersData.sort((a, b) => a.order - b.order))
      } catch (error) {
        console.error("Error fetching data:", error)
        setError("Failed to load content")
      } finally {
        setLoading(false)
      }
    }

    fetchData()
  }, [params.subject])

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <p>Loading...</p>
      </div>
    )
  }

  if (error || !subject) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Card className="p-6 text-center">
          <p className="text-red-500">{error}</p>
          <Button onClick={() => window.location.reload()} className="mt-4">
            Retry
          </Button>
        </Card>
      </div>
    )
  }

  return (
    <main className="min-h-screen bg-gradient-to-br from-orange-50/40 to-white">
      <div className="container mx-auto px-6 py-8 max-w-[1200px]">
        {/* Header */}
        <div className="mb-8">
          <Link 
            href="/against-one-patra" 
            className="text-orange-500 hover:text-orange-600 mb-2 inline-flex items-center gap-2 font-medium"
          >
            ← Back to Subjects
          </Link>
          <h1 className="text-2xl font-bold text-gray-800 mt-4">{subject.name}</h1>
          <p className="text-gray-600 mt-1">Select a chapter to start the quiz</p>
        </div>

        {/* Chapters Grid */}
        <div className="grid grid-cols-1 gap-4">
          {chapters.map((chapter) => (
            <Link 
              key={chapter.id} 
              href={`/against-one-patra/${params.subject}/quiz/${chapter.id}`}
            >
              <Card className="group hover:shadow-lg transition-all duration-300 cursor-pointer bg-white border-none">
                <div className="p-6 relative overflow-hidden">
                  {/* Background Decoration */}
                  <div className="absolute top-0 right-0 w-32 h-32 bg-orange-50 rounded-bl-[140px] transition-all duration-300 group-hover:bg-orange-100/70"></div>
                  
                  {/* Content */}
                  <div className="relative flex items-center justify-between">
                    <div>
                      <div className="flex items-center gap-4 mb-2">
                        <span className="text-sm font-medium text-orange-500 bg-orange-50 px-3 py-1 rounded-full">
                          Chapter {chapter.order}
                        </span>
                        <span className={`text-sm font-medium px-3 py-1 rounded-full ${
                          chapter.difficulty === 'Hard' 
                            ? 'bg-red-50 text-red-500' 
                            : chapter.difficulty === 'Medium'
                            ? 'bg-yellow-50 text-yellow-600'
                            : 'bg-emerald-50 text-emerald-500'
                        }`}>
                          {chapter.difficulty}
                        </span>
                        <span className="text-sm text-gray-500">
                          ⏱️ {chapter.estimatedTime}
                        </span>
                      </div>
                      <h3 className="text-lg font-semibold text-gray-800">{chapter.title}</h3>
                    </div>
                    <Button 
                      className="bg-orange-500 hover:bg-orange-600 text-white rounded-full px-6"
                    >
                      Start Quiz
                    </Button>
                  </div>
                </div>
              </Card>
            </Link>
          ))}
        </div>
      </div>
    </main>
  )
}
